<template>
  <div class="login">
    <div>
      <div>
        <br>
        <div class="column">
          <div class="title is-3 is-6-desktop level-item" text-align="center">
            Mardar sua disponibilidade para o {{this.proposta.proposta.user_envia.user.name}}
            <hr>
          </div>
        </div>
        <br>
        <form action method="POST">
          <div class="columns">
            <div class="column is-offset-3 is-3-desktop is-full-mobile">
              <label for="data" class="label">
                <b>Diga sua disponibilidade durante a semana</b>
              </label>
              <input
                type="text"
                class="input inp"
                v-model="data"
                placeholder="Ex:. 10000 mil reais"
                name="data"
                id="data"
              >
            </div>
            <div class="column is-3-desktop is-full-mobile">
              <label for="horaInicio" class="label">
                <b>Disponibilidade quanto ao tempo de forma geral!</b>
              </label>

              <div class="columns">
                <div class="column is-6-desktop">
                  <b>Das</b>
                  <input
                    type="time"
                    class="input inp column "
                    v-model="horaInicio"
                    name="horaInicio"
                    id="horaInicio"
                  >
                </div>
                <div class="column is-6-desktop">

                  <b>às</b>
                  <input
                    type="time"
                    class="input inp"
                    v-model="horaFim"
                    name="horaFim"
                    id="horaFim"
                  >
                </div>
              </div>
            </div>
          </div>
          <div class="column is-6-desktop is-offset-3 is-full-mobile">
            <label for="descricao" class="label">
              <b>Descreva mais sobre seu tempo, quando você pode atender esse serviço, etc...</b>
            </label>
            <textarea
              name="descricao"
              id="descricao"
              class="textarea inp"
              v-model="descricao"
              placeholder="Ola terraqueo"
              cols="20"
              rows="5"
            ></textarea>
          </div>
          <div class="control column">
            <button
              @click.prevent="addOrcamento"
              class="column is-6-desktop is-offset-3 is-full-mobile btn-1"
            >Enviar sua disponibilidade</button>
          </div>
          <div class="column is-6-desktop is-offset-3 is-full-mobile">
            <div class="obs" style="color:white">
              <b>OBS: O aqui não é um agendamento formal, e sim pra motivos de informações extras. Voçê pode entrar em contato com o(a) {{this.proposta.proposta.user_envia.user.name}} pelo telefone {{this.proposta.proposta.user_envia.telefone}} para informações sobre o agendamento!</b>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
//import gql from "graphql-tag";
import orcamento from "@/graphql/mutations/orcamento.gql";
import propostaForId from "@/graphql/queries/propostaForId.gql";

//import {onLogin} from '../vue-apollo.js'
//import {Bus} from '../event.js'
export default {
  data() {
    return {
      proposta: [],
      data: "",
      horaInicio: "",
      horaFim: "",
      descricao: ""
    };
  },
  apollo: {
    proposta: {
      query: propostaForId,
      variables() {
        return {
          id: this.$route.params.id
        };
      },
      update(data) {
        this.proposta = data;
        return this.proposta;
      }
    }
  },
  methods: {
    addOrcamento() {
      this.$apollo
        .mutate({
          mutation: orcamento,
          variables: {
            data: this.data,
            horaInicio: this.horaInicio,
            horaFim: this.horaFim,
            descricao: this.descricao,
            proposta_id: this.proposta.proposta.id
          }
        })
        .then(() => {
          this.$router.push(`/page/profissional/solicitacoes`);
        })
        .catch(error => {
          console.error(error);
        });
    }
  }
};
</script>
<style>
@import "../../cssFiles/inputsAndButtons.css";

.inp {
  width: 100%;
  height: 3.5em;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}
.inp:focus {
  border: 3px solid rgb(94, 10, 190);
}

.inp1 {
  width: 100%;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}
.inp1:focus {
  border: 3px solid tomato;
}

.obs {
  background: grey;
  padding: 1em 2em;
}
</style>
